import React, { Component } from 'react'
import AccountInfoComp from './Info/AccountInfoComp'
export default class ItemProfileComp extends Component {
    render() {
        return (
            <>
                {/* <!--Tabs navigation--> */}
                <div className='p-7'>
                    <ul
                        class="mb-5 flex list-none flex-row flex-wrap border-b-0 ps-0"
                        role="tablist"
                        data-twe-nav-ref>
                        <li role="presentation">
                            <a
                                href="#tabs-info"
                                class="my-2 block border-x-0 border-b-2 border-t-0 border-transparent px-7 pb-3.5 pt-4 text-xs font-medium uppercase leading-tight text-neutral-500 hover:isolate hover:border-transparent hover:bg-neutral-100 focus:isolate focus:border-transparent data-[twe-nav-active]:border-primary data-[twe-nav-active]:text-primary dark:text-white/50 dark:hover:bg-neutral-700/60 dark:data-[twe-nav-active]:text-primary"
                                data-twe-toggle="pill"
                                data-twe-target="#tabs-info"
                                data-twe-nav-active
                                role="tab"
                                aria-controls="tabs-info"
                                aria-selected="true"
                            >Thông tin tài khoản</a>
                        </li>
                        <li role="presentation">
                            <a
                                href="#tabs-address"
                                class="my-2 block border-x-0 border-b-2 border-t-0 border-transparent px-7 pb-3.5 pt-4 text-xs font-medium uppercase leading-tight text-neutral-500 hover:isolate hover:border-transparent hover:bg-neutral-100 focus:isolate focus:border-transparent data-[twe-nav-active]:border-primary data-[twe-nav-active]:text-primary dark:text-white/50 dark:hover:bg-neutral-700/60 dark:data-[twe-nav-active]:text-primary"
                                data-twe-toggle="pill"
                                data-twe-target="#tabs-address"
                                role="tab"
                                aria-controls="tabs-address"
                                aria-selected="false"
                            >Địa chỉ</a>
                        </li>
                        <li role="presentation">
                            <a
                                href="#tabs-order"
                                class="my-2 block border-x-0 border-b-2 border-t-0 border-transparent px-7 pb-3.5 pt-4 text-xs font-medium uppercase leading-tight text-neutral-500 hover:isolate hover:border-transparent hover:bg-neutral-100 focus:isolate focus:border-transparent data-[twe-nav-active]:border-primary data-[twe-nav-active]:text-primary dark:text-white/50 dark:hover:bg-neutral-700/60 dark:data-[twe-nav-active]:text-primary"
                                data-twe-toggle="pill"
                                data-twe-target="#tabs-order"
                                role="tab"
                                aria-controls="tabs-order"
                                aria-selected="false"
                            >Đơn hàng đã đặt</a>
                        </li>                      
                    </ul>

                    {/* <!--Tabs content--> */}
                    <div class="mb-6">
                        <div
                            class="hidden opacity-100 transition-opacity duration-150 ease-linear data-[twe-tab-active]:block"
                            id="tabs-info"
                            role="tabpanel"
                            aria-labelledby="tabs-info-tab"
                            data-twe-tab-active>
                           <AccountInfoComp/>
                        </div>
                        <div
                            class="hidden opacity-0 transition-opacity duration-150 ease-linear data-[twe-tab-active]:block"
                            id="tabs-address"
                            role="tabpanel"
                            aria-labelledby="tabs-address-tab">
                            Tab 2 content
                        </div>
                        <div
                            class="hidden opacity-0 transition-opacity duration-150 ease-linear data-[twe-tab-active]:block"
                            id="tabs-order"
                            role="tabpanel"
                            aria-labelledby="tabs-order-tab">
                            Tab 3 content
                        </div>
                        
                    </div>
                </div>
            </>
        )
    }
}
