import React, { Component } from 'react'
import ItemProfileComp from '../../components/ProfileComp/ItemProfileComp'
import Navbar from '../../components/Navbar/Navbar';
import {
    Tab,
    initTWE,
} from "tw-elements";

export default class LayoutProfile extends Component {
    componentDidMount() {
        initTWE({ Tab });
    }
    render() {
        return (
            <>

                <Navbar />
                <div
                    className='w-[auto] font-mono p-5 font-bold uppercase bg-slate-700 text-white '
                >
                    Trang tài khoản
                </div>
                <ItemProfileComp />
            </>

        )
    }
}
