import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { getDownloadURL, listAll, ref } from 'firebase/storage';
import { storage } from '../StorageImageText/TxtImageConfig';
import axios from 'axios';
import { toast } from 'react-toastify';

class CheckoutComponent extends Component {

    constructor(props) {
        super(props);
        this.state = {
            dataAddToCheckout: [],
            totalPrice: 0,
            listImageVariants: [],
            address: '',
            delivery_fee: 0,
            selectedPaymentMethod: '',
            provinces: [],
            districts: [],
            wards: [],
            selectedProvince: '',
            selectedDistrict: '',
            selectedWard: ''
        };
        this.listImageVariants = ref(storage, '/images');
    }

    componentDidMount() {
        const data = sessionStorage.getItem('selectedCartItems');
        const totalPrice = sessionStorage.getItem('totalPrice');

        this.setState({
            dataAddToCheckout: JSON.parse(data) && JSON.parse(data) !== null ? JSON.parse(data) : '',
            totalPrice: totalPrice
        });

        this.fetchImageList();
        this.fetchProvinces();
    }

    fetchImageList() {
        listAll(this.listImageVariants).then((response) => {
            const fetchUrls = response.items.map((item) => getDownloadURL(item));
            Promise.all(fetchUrls).then((urls) => {
                this.setState({ listImageVariants: urls });
            });
        });
    }

    fetchProvinces = () => {
        axios.get('https://online-gateway.ghn.vn/shiip/public-api/master-data/province', {
            headers: {
                'Token': '40890c3a-2e2f-11ef-8ba9-b6fbcb92e37e'
            }
        }).then(response => {
            this.setState({ provinces: response.data.data });
        }).catch(error => {
            console.error('Error fetching provinces:', error);
        });
    };

    fetchDistricts = (provinceId) => {
        axios.get(`https://online-gateway.ghn.vn/shiip/public-api/master-data/district?province_id=${provinceId}`, {
            headers: {
                'Token': '40890c3a-2e2f-11ef-8ba9-b6fbcb92e37e'
            }
        }).then(response => {
            this.setState({ districts: response.data.data });
        }).catch(error => {
            console.error('Error fetching districts:', error);
        });
    };

    fetchWards = (districtId) => {
        axios.get(`https://online-gateway.ghn.vn/shiip/public-api/master-data/ward?district_id=${districtId}`, {
            headers: {
                'Token': '40890c3a-2e2f-11ef-8ba9-b6fbcb92e37e'
            }
        }).then(response => {
            this.setState({ wards: response.data.data });
        }).catch(error => {
            console.error('Error fetching wards:', error);
        });
    };

    calculateShippingFee = () => {
        const { selectedDistrict, selectedWard } = this.state;
        if (!selectedDistrict || !selectedWard) {
            return;
        }
        const requestData = {
            service_type_id: 2,
            from_district_id: 1454, // Replace with your shop's district id
            to_district_id: selectedDistrict,
            to_ward_code: selectedWard,
            height: 10,
            length: 10,
            weight: 20,
            width: 20,
            insurance_value: 0,
            coupon: null
        };

        axios.post('https://online-gateway.ghn.vn/shiip/public-api/v2/shipping-order/fee', requestData, {
            headers: {
                'Content-Type': 'application/json',
                'Token': '40890c3a-2e2f-11ef-8ba9-b6fbcb92e37e',
                'ShopId': '5127770'
            }
        }).then(response => {
            if (response.data.code === 200) {
                let shippingFee = parseInt(response.data.data.total);
                this.setState({ delivery_fee: shippingFee });
            } else {
                toast.error('Error calculating shipping fee: ' + response.data.message);
            }
        }).catch(error => {
            console.error('Error calculating shipping fee:', error);
            toast.error('Error calculating shipping fee');
        });
    };

    handleProvinceChange = (event) => {
        const provinceId = event.target.value;
        this.setState({ selectedProvince: provinceId });
        this.fetchDistricts(provinceId);
    };

    handleDistrictChange = (event) => {
        const districtId = event.target.value;
        this.setState({ selectedDistrict: districtId });
        this.fetchWards(districtId);
    };

    handleWardChange = (event) => {
        const wardCode = event.target.value;
        this.setState({ selectedWard: wardCode }, this.calculateShippingFee);
    };

    handlePaymentMethodChange = (event) => {
        this.setState({ selectedPaymentMethod: event.target.id });
    };

    handlePlaceOrder = () => {
        const { dataAddToCheckout, totalPrice, delivery_fee, selectedPaymentMethod, selectedWard } = this.state;
        const checkoutElement = {
            items: dataAddToCheckout,
            totalPrice: parseInt(totalPrice) + parseInt(delivery_fee),
            deliveryFee: delivery_fee,
            address: selectedWard,
            paymentMethod: selectedPaymentMethod,
            state: 1 // Assuming state is fixed for now
        };

        axios.post('http://localhost:8080/api/order/create', checkoutElement)
            .then(response => {
                toast.success('Đặt hàng thành công');
                sessionStorage.removeItem('selectedCartItems');
                sessionStorage.removeItem('totalPrice');
                this.setState({ dataAddToCheckout: [], totalPrice: 0 });
                this.props.history.push('/success');
            })
            .catch(error => {
                console.error('There was an error placing the order:', error);
                toast.error('Đặt hàng thất bại');
            });
    };

    render() {
        const { dataAddToCheckout, totalPrice, delivery_fee, provinces, districts, wards } = this.state;
        const address = dataAddToCheckout.length > 0 ? dataAddToCheckout[0].user.address.full_address : '';

        return (
            <>
                <div className="flex flex-col items-center border-b bg-white py-4 sm:flex-row sm:px-10 lg:px-20 xl:px-32">
                    <button href="#" className="text-2xl font-bold text-gray-800">sneekpeeks</button>
                    <div className="mt-4 py-2 text-xs sm:mt-0 sm:ml-auto sm:text-base"></div>
                </div>
                <div className="grid sm:px-10 lg:grid-cols-2 lg:px-20 xl:px-32">
                    <div className="px-4 pt-8">
                        <p className="text-xl font-medium">Thông tin đơn hàng</p>
                        <p className="text-gray-400">Kiểm tra sản phẩm và lựa chọn phương thức thanh toán.</p>
                        <div className="mt-8 space-y-3 rounded-lg border bg-white px-2 py-4 sm:px-6">
                            {dataAddToCheckout && dataAddToCheckout.length > 0 && dataAddToCheckout !== null ? 
                                dataAddToCheckout.map((item, index) => (
                                    <div className="flex flex-col rounded-lg bg-white sm:flex-row" key={index}>
                                        <img className="m-2 h-24 w-28 rounded-md border object-cover object-center" src={item.variantProd.image_variant} alt="" />
                                        <div className="flex text-start ms-10 w-full flex-col px-4 py-4">
                                            <span className="font-semibold"> {item.variantProd.product.name_prod} - {item.variantProd.size.name_size} - {item.variantProd.color.color_name}</span>
                                            <span className="float-right text-gray-400">SL: {item.quantity} x {(item.variantProd.price).toLocaleString()} </span>
                                            <p className="text-lg font-bold">{(item.quantity * item.variantProd.price).toLocaleString()}</p>
                                        </div>
                                    </div>
                                )) 
                                : 
                                <div>Đơn hàng đang trống</div>
                            }
                        </div>
                    </div>
                    <div className="mt-10 bg-gray-50 px-4 pt-8 lg:mt-0">
                        <p className="text-xl font-medium">Chi tiết thanh toán</p>
                        <p className="text-gray-400">Hãy hoàn thành thông tin đơn hàng của bạn</p>
                        <div>
                            <label htmlFor="billing-address" className="text-start mt-4 mb-2 block text-sm font-medium">Địa chỉ giao hàng</label>
                            <div className="w-full">
                                <div className="relative flex-shrink-0 sm:w-12/12">
                                    <select onChange={this.handleProvinceChange} className="w-full rounded-md border border-gray-200 px-4 py-3 pl-11 text-sm shadow-sm outline-none focus:z-10 focus:border-blue-500 focus:ring-blue-500">
                                        <option value="">Chọn Tỉnh/Thành phố</option>
                                        {provinces.map(province => (
                                            <option key={province.ProvinceID} value={province.ProvinceID}>{province.ProvinceName}</option>
                                        ))}
                                    </select>
                                </div>
                                <div className="relative flex-shrink-0 sm:w-12/12 mt-4">
                                    <select onChange={this.handleDistrictChange} className="w-full rounded-md border border-gray-200 px-4 py-3 pl-11 text-sm shadow-sm outline-none focus:z-10 focus:border-blue-500 focus:ring-blue-500">
                                        <option value="">Chọn Quận/Huyện</option>
                                        {districts.map(district => (
                                            <option key={district.DistrictID} value={district.DistrictID}>{district.DistrictName}</option>
                                        ))}
                                    </select>
                                </div>
                                <div className="relative flex-shrink-0 sm:w-12/12 mt-4">
                                    <select onChange={this.handleWardChange} className="w-full rounded-md border border-gray-200 px-4 py-3 pl-11 text-sm shadow-sm outline-none focus:z-10 focus:border-blue-500 focus:ring-blue-500">
                                        <option value="">Chọn Xã/Phường</option>
                                        {wards.map(ward => (
                                            <option key={ward.WardCode} value={ward.WardCode}>{ward.WardName}</option>
                                        ))}
                                    </select>
                                </div>
                                <div className="relative flex-shrink-0 sm:w-12/12 mt-4">
                                    <input value={address} type="text" id="billing-address" name="billing-address" className="w-full rounded-md border border-gray-200 px-4 py-3 pl-11 text-sm shadow-sm outline-none focus:z-10 focus:border-blue-500 focus:ring-blue-500" placeholder="Street Address" />
                                </div>
                            </div>
                            <div>
                                <p className="mt-8 text-lg font-medium">Phương thức thanh toán</p>
                                <form className="mt-5 grid gap-6">
                                    <div className="relative">
                                        <input className="peer hidden" id="1" type="radio" name="radio" onChange={this.handlePaymentMethodChange} />
                                        <span className="peer-checked:border-gray-700 absolute right-4 top-1/2 box-content block h-3 w-3 -translate-y-1/2 rounded-full border-8 border-gray-300 bg-white"></span>
                                        <label className="peer-checked:border-2 peer-checked:border-gray-700 peer-checked:bg-gray-50 flex cursor-pointer select-none rounded-lg border border-gray-300 p-4" htmlFor="1">
                                            <img className="w-14 object-contain" src="https://cdn-icons-png.flaticon.com/512/2897/2897855.png" alt="" />
                                            <div className="ml-5">
                                                <span className="mt-2 font-semibold">Thanh toán khi nhận hàng</span>
                                                <p className="text-slate-500 text-sm leading-6">Khi đơn hàng được giao</p>
                                            </div>
                                        </label>
                                    </div>
                                    <div className="relative">
                                        <input className="peer hidden" id="2" type="radio" name="radio" onChange={this.handlePaymentMethodChange} />
                                        <span className="peer-checked:border-gray-700 absolute right-4 top-1/2 box-content block h-3 w-3 -translate-y-1/2 rounded-full border-8 border-gray-300 bg-white"></span>
                                        <label className="peer-checked:border-2 peer-checked:border-gray-700 peer-checked:bg-gray-50 flex cursor-pointer select-none rounded-lg border border-gray-300 p-4" htmlFor="2">
                                            <img className="w-14 object-contain" src="https://vnpay.vn/s1/statics.vnpay.vn/2023/6/0oxhzjmxbksr1686814746087.png" alt="" />
                                            <div className="ml-5">
                                                <span className="mt-2 font-semibold">Thanh toán qua VNPay</span>
                                                <p className="text-slate-500 text-sm leading-6">Thanh toán trực tiếp</p>
                                            </div>
                                        </label>
                                    </div>
                                </form>
                            </div>
                        </div>
                        <div className="mt-6 border-t border-b py-2">
                            <div className="flex items-center justify-between">
                                <p className="text-sm font-medium text-gray-900">Tạm tính</p>
                                <p className="font-semibold text-gray-900">{parseInt(totalPrice).toLocaleString()}</p>
                            </div>
                            <div className="flex items-center justify-between">
                                <p className="text-sm font-medium text-gray-900">Phí vận chuyển</p>
                                <p className="font-semibold text-gray-900">{delivery_fee.toLocaleString()}</p>
                            </div>
                        </div>
                        <div className="mt-6 flex items-center justify-between">
                            <p className="text-sm font-medium text-gray-900">Tổng cộng</p>
                            <p className="text-2xl font-semibold text-gray-900">{(parseInt(totalPrice) + parseInt(delivery_fee)).toLocaleString()}</p>
                        </div>
                        <button onClick={this.handlePlaceOrder} className="mt-4 mb-8 w-full rounded-md bg-gray-900 px-6 py-3 font-medium text-white">Đặt hàng</button>
                    </div>
                </div>
            </>
        );
    }
}

export default withRouter(CheckoutComponent);
