import React, { useState, useEffect, Component } from 'react'
import { storage } from '../StorageImageText/TxtImageConfig';
import { listAll, ref, uploadBytes, getDownloadURL } from 'firebase/storage';
import axios from "axios";
import { withRouter } from 'react-router-dom/cjs/react-router-dom.min';
class ListProduct extends Component {

    constructor(props) {
        super(props);
        this.state = {
            imageUpload: null,
            imageList: [],
            listProduct: []
        };
        this.imageListRef = ref(storage, "images/");
        this.fetchImageList = this.fetchImageList.bind(this);
    }


    async componentDidMount() {
        let res = await axios.get("http://localhost:8080/api/products1");
        console.log(res);
        this.setState({ listProduct: res ? res.data : [] });
        console.log(this.state.listProduct);
        this.fetchImageList();
    }

    handleViewDetail = (item) => {
        this.props.history.push(`/product/${item.id}`)
    }

    fetchImageList() {
        listAll(this.imageListRef).then((response) => {
            const fetchUrls = response.items.map((item) => getDownloadURL(item));
            Promise.all(fetchUrls).then((urls) => {
                this.setState({ imageList: urls });
            });
        });
    }

    render() {
        let { listProduct } = this.state;
        return (
            <>
                <div class="mx-auto max-w-2xl px-4 py-16 sm:px-6 sm:py-24 lg:max-w-7xl lg:px-8">
                    <h2 class="text-2xl font-bold tracking-tight text-gray-900 uppercase">
                        Những mặt hàng nổi bật
                    </h2>
                    <div
                        class="mt-6 grid grid-cols-1 gap-x-6 gap-y-10 sm:grid-cols-2 lg:grid-cols-4 xl:gap-x-8">
                        {
                            listProduct && listProduct.length > 0 && listProduct.map((item, index) => {
                                return (

                                    <div class="group relative">
                                        <div class="aspect-h-1 aspect-w-1 w-full overflow-hidden rounded-md bg-gray-200 lg:aspect-none group-hover:opacity-75 lg:h-80">
                                            <img key={index} src={item.image_prod} alt="Front of men&#039;s Basic Tee in black." class="h-full w-full object-cover object-center lg:h-full lg:w-full" />
                                        </div>
                                        <div class="mt-4 flex justify-between">
                                            <div>
                                                <h3 class="text-sm text-gray-700">
                                                    <button >
                                                        <span onClick={() => this.handleViewDetail(item)} aria-hidden="true" class="absolute inset-0"></span>
                                                        {item.name_prod}
                                                    </button>
                                                </h3>
                                                <p class="mt-1 text-sm text-gray-500">Black</p>
                                            </div>
                                            <p class="text-sm font-medium text-gray-900">item.price</p>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            </>
        )
    }
}
export default withRouter(ListProduct)