import axios from 'axios'
import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { getDownloadURL, listAll, ref } from 'firebase/storage';
import { storage } from '../StorageImageText/TxtImageConfig';
import { toast } from 'react-toastify';
import { NavLink } from 'react-router-dom';

class CartDetailComp extends Component {

    constructor(props) {
        super(props)
        this.state = {
            cartData: [],
            imageListVariants: [],
            nameP: '',
            totalPrice: 0,
            selectAllChecked: false,
        }
        this.imageListVariantsRef = ref(storage, 'images/');
    }

    async componentDidMount() {
        const user = JSON.parse(localStorage.getItem('userData'))
        const resp = await axios.get(`http://localhost:8080/api/cartdetail/${user.id_user}`)
        const name_prod = localStorage.getItem('name_prod')

        this.setState({
            cartData: resp && resp.data ? resp.data.map(item => ({ ...item, checked: false })) : [],
            nameP: name_prod,
        })

        this.fetchImageList();
    }

    fetchImageList() {
        listAll(this.imageListVariantsRef).then((response) => {
            const fetchUrls = response.items.map((item) => getDownloadURL(item));
            Promise.all(fetchUrls).then((urls) => {
                this.setState({ imageListVariants: urls });
            });
        });
    }

    handleQuantityChange = (index, quantity) => {
        this.setState((prevState) => {
            const newCartData = prevState.cartData.map((item, i) => {
                if (i === index) {
                    const maxQuantity = item.variantProd.quantity;
                    return {
                        ...item,
                        quantity: quantity > maxQuantity ? maxQuantity : quantity < 1 ? 1 : quantity
                    };
                }
                return item;
            });
            return { cartData: newCartData };
        }, () => {
            console.log(this.state.cartData[index].quantity);
            this.calculateTotalPrice();
        });
    }

    incrementQuantity = (index, event) => {
        event.stopPropagation();
        this.setState((prevState) => {
            const newCartData = prevState.cartData.map((item, i) => {
                if (i === index) {
                    if (item.quantity < item.variantProd.quantity) {
                        return { ...item, quantity: item.quantity + 1 }
                    } else
                        return {
                            ...item, quantity: item.quantity
                        }
                }
                return item;
            });
            return { cartData: newCartData };
        }, () => {
            console.log(this.state.cartData[index].quantity);
            this.calculateTotalPrice();
        });
    }

    decrementQuantity = (index, event) => {
        event.stopPropagation();
        console.log('Decrement clicked');
        this.setState((prevState) => {
            const newCartData = prevState.cartData.map((item, i) => {
                if (i === index) {
                    return {
                        ...item,
                        quantity: item.quantity > 1 ? item.quantity - 1 : item.quantity
                    };
                }
                return item;
            });
            return { cartData: newCartData };
        }, () => {
            console.log(this.state.cartData[index].quantity);
            this.calculateTotalPrice();
        });
    }

    handleCheckboxChange = (index) => {
        this.setState((prevState) => {
            const newCartData = prevState.cartData.map((item, i) => {
                if (i === index) {
                    return { ...item, checked: !item.checked };
                }
                return item;
            });
            return { cartData: newCartData };
        }, () => {
            this.calculateTotalPrice();
        });
    }

    async handleDeleteCartItem(id) {
        try {
            await axios.delete(`http://localhost:8080/api/cartdetail/delete/${id}`);
            toast.success("Delete successfully");

            // Fetch updated data
            const user = JSON.parse(localStorage.getItem('userData'));
            const resp = await axios.get(`http://localhost:8080/api/cartdetail/${user.id_user}`);
            this.setState({
                cartData: resp && resp.data ? resp.data.map(item => ({ ...item, checked: false })) : []
            });
            this.calculateTotalPrice();
        } catch (error) {
            toast.error("Delete failed");
            console.error("There was an error deleting the cart item!", error);
        }
    }

    handleSelectAllChange = () => {
        this.setState((prevState) => {
            const newSelectAllChecked = !prevState.selectAllChecked;
            const newCartData = prevState.cartData.map((item) => ({
                ...item,
                checked: newSelectAllChecked
            }));
            return {
                cartData: newCartData,
                selectAllChecked: newSelectAllChecked,
            };
        }, () => {
            this.calculateTotalPrice();
        });
    }

    calculateTotalPrice = () => {
        const totalPrice = this.state.cartData.reduce((acc, item) => {
            if (item.checked) {
                return acc + item.quantity * item.variantProd.price;
            }
            return acc;
        }, 0);
        this.setState({ totalPrice });
    }

    saveSelectedItemsToSession = () => {
        
        const selectedItems = this.state.cartData.filter(item => item.checked);
        if(selectedItems.length <= 0) {
            toast.error("Không có mặt hàng để mua");
            return
        }else{
            sessionStorage.setItem('selectedCartItems', JSON.stringify(selectedItems));
            sessionStorage.setItem('totalPrice', this.state.totalPrice);
        }
       
    }

    render() {
        const { cartData, nameP, totalPrice, selectAllChecked } = this.state;

        return (
            <>
                <div class="font-sans max-w-5xl max-md:max-w-xl mx-auto bg-white py-4">
                    <h1 class="mt-16 text-3xl uppercase font-bold text-gray-800 text-center">Giỏ hàng của tôi</h1>

                    <div class="flex justify-start">
                        {/* checkbox chọn tất cả các mặt hàng cần đặt hàng*/}
                        <div class=" me-4 inline-block min-h-[1.5rem] ps-[1.5rem]">
                            <input
                                class="relative float-left -ms-[1.5rem] me-[6px] mt-[0.15rem] h-[1.125rem] w-[1.125rem] appearance-none rounded-[0.25rem] border-[0.125rem] border-solid border-secondary-500 outline-none before:pointer-events-none before:absolute before:h-[0.875rem] before:w-[0.875rem] before:scale-0 before:rounded-full before:bg-transparent before:opacity-0 before:shadow-checkbox before:shadow-transparent before:content-[''] checked:border-primary checked:bg-primary checked:before:opacity-[0.16] checked:after:absolute checked:after:-mt-px checked:after:ms-[0.25rem] checked:after:block checked:after:h-[0.8125rem] checked:after:w-[0.375rem] checked:after:rotate-45 checked:after:border-[0.125rem] checked:after:border-l-0 checked:after:border-t-0 checked:after:border-solid checked:after:border-white checked:after:bg-transparent checked:after:content-[''] hover:cursor-pointer hover:before:opacity-[0.04] hover:before:shadow-black/60 focus:shadow-none focus:transition-[border-color_0.2s] focus:before:scale-100 focus:before:opacity-[0.12] focus:before:shadow-black/60 focus:before:transition-[box-shadow_0.2s,transform_0.2s] focus:after:absolute focus:after:z-[1] focus:after:block focus:after:h-[0.875rem] focus:after:w-[0.875rem] focus:after:rounded-[0.125rem] focus:after:content-[''] checked:focus:before:scale-100 checked:focus:before:shadow-checkbox checked:focus:before:transition-[box-shadow_0.2s,transform_0.2s] checked:focus:after:-mt-px checked:focus:after:ms-[0.25rem] checked:focus:after:h-[0.8125rem] checked:focus:after:w-[0.375rem] checked:focus:after:rotate-45 checked:focus:after:rounded-none checked:focus:after:border-[0.125rem] checked:focus:after:border-l-0 checked:focus:after:border-t-0 checked:focus:after:border-solid checked:focus:after:border-white checked:focus:after:bg-transparent rtl:float-right dark:border-neutral-400 dark:checked:border-primary dark:checked:bg-primary"
                                type="checkbox"
                                id="inlineCheckbox1"
                                value="option1"
                                checked={selectAllChecked}
                                onChange={this.handleSelectAllChange}
                            />
                            <label
                                class="inline-block font-semibold uppercase ps-[0.15rem] hover:cursor-pointer"
                                for="inlineCheckbox1"
                            >Chon tat ca</label>
                        </div>
                        {/* đóng chọn tất cả các mặt hàng cần đặt hàng*/}
                    </div>



                    <div class=" gap-8 mt-16" >
                        <div class=" space-y-4 align-middle">
                            {/* fetch Data Bien the load vao element */}
                            {cartData && cartData.length > 0 && cartData != null ? cartData.map((item, index) =>
                            (
                                <>
                                    <div id={item.id} key={item.id} class="grid grid-cols-4 items-start ">
                                        <div class="col-span-2 flex items-start gap-4 ">
                                            {/* checkbox chọn sản phẩm cần đặt hàng */}
                                            <div class="mb-[0.125rem] block min-h-[1.5rem] ps-[1.5rem] mt-10">
                                                <input
                                                    class=" -ms-[1.5rem] me-[6px] mt-[0.15rem] h-[1.125rem] w-[1.125rem] appearance-none rounded-[0.25rem] border-[0.125rem] border-solid border-secondary-500 outline-none before:pointer-events-none before:absolute before:h-[0.875rem] before:w-[0.875rem] before:scale-0 before:rounded-full before:bg-transparent before:opacity-0 before:shadow-checkbox before:shadow-transparent before:content-[''] checked:border-primary checked:bg-primary checked:before:opacity-[0.16] checked:after:absolute checked:after:-mt-px checked:after:ms-[0.25rem] checked:after:block checked:after:h-[0.8125rem] checked:after:w-[0.375rem] checked:after:rotate-45 checked:after:border-[0.125rem] checked:after:border-l-0 checked:after:border-t-0 checked:after:border-solid checked:after:border-white checked:after:bg-transparent checked:after:content-[''] hover:cursor-pointer hover:before:opacity-[0.04] hover:before:shadow-black/60 focus:shadow-none focus:transition-[border-color_0.2s] focus:before:scale-100 focus:before:opacity-[0.12] focus:before:shadow-black/60 focus:before:transition-[box-shadow_0.2s,transform_0.2s] focus:after:absolute focus:after:z-[1] focus:after:block focus:after:h-[0.875rem] focus:after:w-[0.875rem] focus:after:rounded-[0.125rem] focus:after:content-[''] checked:focus:before:scale-100 checked:focus:before:shadow-checkbox checked:focus:before:transition-[box-shadow_0.2s,transform_0.2s] checked:focus:after:-mt-px checked:focus:after:ms-[0.25rem] checked:focus:after:h-[0.8125rem] checked:focus:after:w-[0.375rem] checked:focus:after:rotate-45 checked:focus:after:rounded-none checked:focus:after:border-[0.125rem] checked:focus:after:border-l-0 checked:focus:after:border-t-0 checked:focus:after:border-solid checked:focus:after:border-white checked:focus:after:bg-transparent rtl:float-right dark:border-neutral-400 dark:checked:border-primary dark:checked:bg-primary"
                                                    type="checkbox"
                                                    value=""
                                                    id={`checkbox-${item.id}`}
                                                    checked={item.checked}
                                                    onChange={() => this.handleCheckboxChange(index)}
                                                />
                                            </div>
                                            {/* checkbox đóng chọn sản phẩm cần đặt hàng */}
                                            <div class="w-28 h-28 max-sm:w-24 max-sm:h-24 shrink-0 bg-gray-100 p-2 rounded-md">
                                                <img src={item.variantProd.image_variant} className="w-full h-full object-contain" alt='' />
                                            </div>
                                            <div class="flex flex-col">
                                                <h3 class="text-base font-bold text-gray-800">{nameP} {item.variantProd.size.name_size} - {item.variantProd.color.color_name}</h3>


                                                <p class="text-lg font-semibold text-red-500 mt-0.5">Price: {item.variantProd.price}</p>

                                                <button onClick={() => this.handleDeleteCartItem(item.id)} type="button" class="mt-6 font-semibold text-blue-500 text-xs flex items-center gap-1 shrink-0">
                                                    <svg xmlns="http://www.w3.org/2000/svg" class="w-4 fill-current inline" viewBox="0 0 24 24">
                                                        <path d="M19 7a1 1 0 0 0-1 1v11.191A1.92 1.92 0 0 1 15.99 21H8.01A1.92 1.92 0 0 1 6 19.191V8a1 1 0 0 0-2 0v11.191A3.918 3.918 0 0 0 8.01 23h7.98A3.918 3.918 0 0 0 20 19.191V8a1 1 0 0 0-1-1Zm1-3h-4V2a1 1 0 0 0-1-1H9a1 1 0 0 0-1 1v2H4a1 1 0 0 0 0 2h16a1 1 0 0 0 0-2ZM10 4V3h4v1Z" data-original="#000000"></path>
                                                        <path d="M11 17v-7a1 1 0 0 0-2 0v7a1 1 0 0 0 2 0Zm4 0v-7a1 1 0 0 0-2 0v7a1 1 0 0 0 2 0Z" data-original="#000000"></path>
                                                    </svg>
                                                    XÓA
                                                </button>
                                            </div>
                                        </div>

                                        <div class="ml-auto">


                                            <div class="flex items-center max-[500px]:justify-center h-full max-md:mt-3">
                                                <div class="flex mt-6 items-center h-full">
                                                    <button
                                                        onClick={(e) => this.decrementQuantity(index, e)}
                                                        class="group rounded-l-xl px-5 py-[18px] border border-gray-200 flex items-center justify-center shadow-sm shadow-transparent transition-all duration-500 hover:bg-gray-50 hover:border-gray-300 hover:shadow-gray-300 focus-within:outline-gray-300">
                                                        <svg class="stroke-gray-900 transition-all duration-500 group-hover:stroke-black"
                                                            xmlns="http://www.w3.org/2000/svg" width="22" height="22"
                                                            viewBox="0 0 22 22" fill="none">
                                                            <path d="M16.5 11H5.5" stroke="" stroke-width="1.6"
                                                                stroke-linecap="round" />
                                                            <path d="M16.5 11H5.5" stroke="" stroke-opacity="0.2" stroke-width="1.6"
                                                                stroke-linecap="round" />
                                                            <path d="M16.5 11H5.5" stroke="" stroke-opacity="0.2" stroke-width="1.6"
                                                                stroke-linecap="round"
                                                            />
                                                        </svg>
                                                    </button>
                                                    <input type="number"
                                                        class="border-y border-gray-200 outline-none text-gray-900 font-semibold text-lg w-full max-w-[73px] min-w-[60px] placeholder:text-gray-900 py-[15px]  text-center bg-transparent
                                                         [appearance:textfield] [&::-webkit-outer-spin-button]:appearance-none [&::-webkit-inner-spin-button]:appearance-none
                                                        "
                                                        value={item.quantity}
                                                        min="1"
                                                        max={item.variantProd.quantity}
                                                        onChange={(e) => this.handleQuantityChange(index, parseInt(e.target.value))}
                                                    />
                                                    <button
                                                        onClick={(e) => this.incrementQuantity(index, e)}
                                                        class="group rounded-r-xl px-5 py-[18px] border border-gray-200 flex items-center justify-center shadow-sm shadow-transparent transition-all duration-500 hover:bg-gray-50 hover:border-gray-300 hover:shadow-gray-300 focus-within:outline-gray-300">
                                                        <svg class="stroke-gray-900 transition-all duration-500 group-hover:stroke-black"
                                                            xmlns="http://www.w3.org/2000/svg" width="22" height="22"
                                                            viewBox="0 0 22 22" fill="none">
                                                            <path d="M11 5.5V16.5M16.5 11H5.5" stroke="" stroke-width="1.6"
                                                                stroke-linecap="round" />
                                                            <path d="M11 5.5V16.5M16.5 11H5.5" stroke="" stroke-opacity="0.2"
                                                                stroke-width="1.6" stroke-linecap="round" />
                                                            <path d="M11 5.5V16.5M16.5 11H5.5" stroke="" stroke-opacity="0.2"
                                                                stroke-width="1.6" stroke-linecap="round" />
                                                        </svg>
                                                    </button>
                                                </div>
                                            </div>

                                        </div>

                                        {/* lấy giá nhân số lượng */}
                                        <div className=' max-[500px]:justify-center h-full max-md:mt-3'>
                                            <h4 class="text-lg mt-10 items-center max-sm:text-base font-bold text-gray-800">
                                                {(item.quantity * item.variantProd.price).toLocaleString()} VND </h4>
                                        </div>
                                    </div>
                                    <hr class="border-gray-300" />
                                </>
                            ))
                                :
                                <>
                                    <div className=' item-center inline-block justify-center'>
                                        <div className='w-full'>
                                            <img className='' src='https://www.ruouhungdong.vn/assets/images/no-cart.png' alt='' />
                                        </div>

                                        <div className='text-4xl uppercase text-center mt-3'>Giỏ hàng trống</div>   
                                    </div>

                                </>
                            }


                        </div>



                        <div class="bg-gray-100 rounded-md p-4 h-max">
                            <h3 class="text-lg max-sm:text-base font-bold text-gray-800 border-b border-gray-300 pb-2">Thông tin giỏ hàng</h3>
                            {/* Tính tổng tiền */}
                            <ul class="text-gray-800  mt-6 space-y-3">
                                <li class="flex font-extrabold text-3xl flex-wrap gap-4 ">
                                    Tổng tiền:  <span class="ml-auto text-red-500">{totalPrice.toLocaleString()} </span>VND
                                </li>
                            </ul>
                            {/* Đóng Tính tổng tiền */}
                            <div class="mt-6 space-y-3">

                                <NavLink to="/checkout">
                                    <button class="text-sm px-4 py-2.5 w-full font-semibold tracking-wide bg-gray-800 hover:bg-gray-900 text-white rounded-md"
                                        onClick={this.saveSelectedItemsToSession}
                                    >
                                        Đặt hàng
                                    </button>
                                </NavLink>
                                <button type="button" class="text-sm px-4 py-2.5 w-full font-semibold tracking-wide bg-transparent text-gray-800 border border-gray-300 rounded-md">Quay lại cửa hàng  </button>
                            </div>
                        </div>
                    </div>
                </div>

            </>
        )
    }
}

export default withRouter(CartDetailComp)








