
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import axios from 'axios';
import { getDownloadURL, listAll, ref } from 'firebase/storage';
import { storage } from '../StorageImageText/TxtImageConfig';
import { toast } from 'react-toastify';

class ProdDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            listVariants: [],
            imageListVariants: [],
            imageProduct: {},
            product: {},
            selectedColorId: null,
            selectedSizeId: null,
            priceVariants: 0,
            availableProducts: 0,
            availableColors: [],
            quantity: 1,
            id_variant: null, // Thêm state để lưu id_variant
        };
        this.handleSizeChange = this.handleSizeChange.bind(this);
        this.imageListVariantsRef = ref(storage, 'images/');
        this.imageProductRef = ref(storage, 'images/');
        this.fetchImageList = this.fetchImageList.bind(this);
        this.handleColorChange = this.handleColorChange.bind(this);
        this.handleAddToCart = this.handleAddToCart.bind(this);
    }

    handleUpQuantity = () => {
        if (this.state.availableProducts === this.state.quantity) {
            toast.error('Số lượng vượt quá sản phẩm cho phép')
            this.setState({
                quantity: this.state.availableProducts
            })
            return
        } else {
            this.setState({
                quantity: parseInt(this.state.quantity + 1)
            })
        }

    }

    handleDownQuantity = () => {
        this.setState({
            quantity: parseInt(this.state.quantity === 1 ? 1 : this.state.quantity - 1),
        })
    }

    handleInputOnchange = (e) => {
        this.setState({
            quantity: e.target.value
        })
    }

    async componentDidMount() {
        if (this.props.match && this.props.match.params) {
            let id = this.props.match.params.id;
            let res = await axios.get(`http://localhost:8080/api/variants/${id}`);
            let resProd = await axios.get(`http://localhost:8080/api/products/${id}`);

            const variants = res && res.data ? res.data : [];
            const product = resProd && resProd.data ? resProd.data : {};
            localStorage.setItem('name_prod', product.name_prod);
            this.setState(
                {
                    listVariants: variants,
                    imageProduct: product,
                    product: product,
                },
                () => {
                    if (variants.length > 0) {
                        const firstVariant = variants[0];
                        const availableColors = variants
                            .filter((item) => item.size.id === firstVariant.size.id && item.quantity > 0)
                            .map((item) => item.color);

                        this.setState({
                            selectedColorId: firstVariant.color.id,
                            selectedSizeId: firstVariant.size.id,
                            priceVariants: firstVariant.price,
                            availableProducts: firstVariant.quantity,
                            availableColors: availableColors,
                            id_variant: firstVariant.id // Lưu id_variant đầu tiên
                        });
                    }
                }
            );
        }
        this.fetchImageList();
    }

    handleColorChange(event) {
        const colorId = event.target.value;
        this.setState({ selectedColorId: colorId }, this.updateProductDetails);
    }

    handleSizeChange(event) {
        const sizeId = event.target.value;
        const { listVariants } = this.state;
        const availableColors = listVariants
            .filter((item) => item.size.id === parseInt(sizeId) && item.quantity > 0)
            .map((item) => item.color);

        this.setState(
            {
                selectedSizeId: sizeId,
                availableColors: availableColors,
                selectedColorId: availableColors.length > 0 ? availableColors[0].id : null,
            },
            this.updateProductDetails
        );
    }

    updateProductDetails() {
        const { selectedColorId, selectedSizeId, listVariants } = this.state;
        if (selectedColorId && selectedSizeId) {
            const matchingVariant = listVariants.find(
                (item) =>
                    item.color.id === parseInt(selectedColorId) && item.size.id === parseInt(selectedSizeId)
            );
            if (matchingVariant) {
                this.setState({
                    availableProducts: matchingVariant.quantity,
                    priceVariants: matchingVariant.price,
                    id_variant: matchingVariant.id // Lưu id_variant tương ứng
                });
            } else {
                this.setState({ availableProducts: 0, priceVariants: 0, id_variant: null });
            }
        }
    }

    fetchImageList() {
        listAll(this.imageListVariantsRef).then((response) => {
            const fetchUrls = response.items.map((item) => getDownloadURL(item));
            Promise.all(fetchUrls).then((urls) => {
                this.setState({ imageListVariants: urls });
            });
        });
    }

    async handleAddToCart() {
        const user = JSON.parse(localStorage.getItem('userData'));
        const { id_variant, quantity } = this.state;

        if (!user || !user.id_user) {
            toast.error('Bạn cần đăng nhập để thêm sản phẩm vào giỏ hàng');
            return;
        }

        if (!id_variant) {
            toast.error('Vui lòng chọn kích thước và màu sắc sản phẩm');
            return;
        }

        const data = {
            id_user: user.id_user,
            id_variant: id_variant,
            quantity: quantity,
        };

        try {
            const response = await axios.post('http://localhost:8080/api/cartdetail/post', data);
            if (response.status === 200) {
                toast.success('Sản phẩm đã được thêm vào giỏ hàng');
            } else {
                toast.error('Có lỗi xảy ra khi thêm sản phẩm vào giỏ hàng');
            }
        } catch (error) {
            toast.error('Có lỗi xảy ra khi thêm sản phẩm vào giỏ hàng');
        }
    }

    render() {
        const {
            listVariants,
            imageProduct,
            product,
            availableProducts,
            priceVariants,
            selectedSizeId,
            selectedColorId,
            availableColors,
            id_variant
        } = this.state;
        const brand = product.brand || {};
        const category = product.category || {};
        console.log(id_variant);
        return (
            <>
                <div className="container grid grid-cols-2 gap-6 p-52 pt-20">
                    <div>
                        <img
                            src={imageProduct.image_prod}
                            alt="product"
                            className="w-full h-4/6"
                        />
                        <div className="grid grid-cols-4 gap-4 mt-4">
                            {listVariants &&
                                listVariants.length > 0 &&
                                listVariants.map((item, index) => (
                                    <img
                                        key={index}
                                        src={item.image_variant}
                                        alt="product2"
                                        className="w-full cursor-pointer border border-primary"
                                    />
                                ))}
                        </div>
                    </div>
                    <div className="text-start">
                        <h2 className="text-3xl font-medium uppercase mb-2">{product.name_prod}</h2>
                        <div>
                            <div className="space-y-2">
                                <p className="text-gray-800 font-semibold space-x-2">
                                    <span>Availability: </span>
                                    <span className="text-green-600">
                                        {product.sum_quantity > 0 ? 'Còn hàng' : 'Hết hàng'}
                                    </span>
                                </p>
                                <p className="space-x-2">
                                    <span className="text-gray-800 font-semibold">Brand: </span>
                                    <span className="text-gray-600">{brand.name_brand}</span>
                                </p>
                                <p className="space-x-2">
                                    <span className="text-gray-800 font-semibold">Category: </span>
                                    <span className="text-gray-600">{category.name_cate}</span>
                                </p>
                                <p className="space-x-2">
                                    <span className="text-gray-800 font-semibold">SKU: </span>
                                    <span className="text-gray-600">BE45VGRT</span>
                                </p>
                            </div>
                        </div>
                        <div className="flex items-baseline mb-1 space-x-2 font-roboto mt-4">
                            <p className="text-xl text-primary font-semibold">$ {priceVariants}</p>
                            <p className="text-base text-gray-400 line-through">$55.00</p>
                        </div>
                        <p className="mt-4 text-gray-600">
                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Eos eius eum
                            reprehenderit dolore vel mollitia optio consequatur hic asperiores inventore
                            suscipit, velit consequuntur, voluptate doloremque iure necessitatibus adipisci
                            magnam porro.
                        </p>
                        <div className="pt-4">
                            <h3 className="text-xl text-gray-800 font-medium uppercase mb-1">Size</h3>
                            <form className="mx-auto">
                                <label
                                    htmlFor="item"
                                    className="block mb-2 text-sm font-medium text-gray-900 dark:text-white"
                                >
                                    Select an option
                                </label>
                                <select
                                    id="item"
                                    className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                                    onChange={this.handleSizeChange}
                                    value={selectedSizeId}
                                >

                                    {listVariants &&
                                        listVariants.length > 0 &&
                                        [
                                            ...new Map(
                                                listVariants.map((item) => [item.size.id, item])
                                            ).values(),
                                        ].map((item) => (
                                            <option key={item.size.id} value={item.size.id}>
                                                {item.size.name_size}
                                            </option>
                                        ))}
                                </select>
                            </form>
                        </div>
                        <div className="pt-4">
                            <h3 className="text-xl text-gray-800 mb-3 uppercase font-medium">Color</h3>
                            <form className="mx-auto">
                                <label
                                    htmlFor="countries"
                                    className="block mb-2 text-sm font-medium text-gray-900 dark:text-white"
                                >
                                    Select an option
                                </label>
                                <select
                                    id="countries"
                                    className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                                    onChange={this.handleColorChange}
                                    value={selectedColorId}
                                >

                                    {availableColors &&
                                        availableColors.length > 0 &&
                                        availableColors.map((color) => (
                                            <option key={color.id} value={color.id}>
                                                {color.color_name}
                                            </option>
                                        ))}
                                </select>
                            </form>
                        </div>
                        <div className="pt-4">
                            <h3 className="text-xl text-gray-800 font-medium uppercase mb-1">Quantity</h3>
                            <div className="flex">
                                <div className="flex border border-gray-300 text-gray-600 divide-x divide-gray-300 w-max">
                                    <button onClick={this.handleDownQuantity} className="h-8 w-8 text-xl flex items-center justify-center cursor-pointer select-none">
                                        -
                                    </button>

                                    <input onChange={this.handleInputOnchange} type="number"
                                        className="text-gray-900 font-semibold h-8 w-8 text-base flex items-center justify-center text-center min-w-[60px] placeholder:text-gray-900 py-[15px]
                                        [appearance:textfield] [&::-webkit-outer-spin-button]:appearance-none [&::-webkit-inner-spin-button]:appearance-none
                                        "
                                        placeholder=""
                                        value={this.state.quantity}
                                    />
                                    <button onClick={this.handleUpQuantity} className="h-8 w-8 text-xl flex items-center justify-center cursor-pointer select-none">
                                        +
                                    </button>
                                </div>
                                <div className="mt-1 ms-2">
                                    {' '}
                                    <span className="font-bold">{availableProducts}</span> Sản phẩm sẵn có
                                </div>
                            </div>
                        </div>
                        <div className="flex gap-3 border-b border-gray-200 pb-5 pt-5">
                            <button onClick={this.handleAddToCart} className="bg-black border border-black text-white px-8 py-2 font-medium rounded uppercase flex items-center gap-2 hover:bg-transparent hover:text-primary transition">
                                <i className="fa-solid fa-bag-shopping"></i> Add to cart
                            </button>
                            <button className="border border-gray-300 text-gray-600 px-8 py-2 font-medium rounded uppercase flex items-center gap-2 hover:text-primary transition">
                                <i className="fa-solid fa-heart"></i> Wishlist
                            </button>
                        </div>
                        <div className="flex gap-3 mt-4">
                            <button className="text-gray-400 hover:text-gray-500 h-8 w-8 rounded-full border border-gray-300 flex items-center justify-center">
                                <i className="fa-brands fa-facebook-f"></i>
                            </button>
                            <button className="text-gray-400 hover:text-gray-500 h-8 w-8 rounded-full border border-gray-300 flex items-center justify-center">
                                <i className="fa-brands fa-twitter"></i>
                            </button>
                            <button className="text-gray-400 hover:text-gray-500 h-8 w-8 rounded-full border border-gray-300 flex items-center justify-center">
                                <i className="fa-brands fa-instagram"></i>
                            </button>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}

export default withRouter(ProdDetail);











